	module.exports = {"data":{"wordpressPost":{"title":"A skin doctor&#8217;s tips for model perfect skin","content":"<p>Yes your BFF knows a lot about skincare. So do you for that matter. But nobody knows quite so much as your friendly dermatologist. A highly-sought after doctor and ambassador for skincare brand Neutrogena, <a href=\"http://www.drninawines.com\">Dr Nina Wines</a> knows how to get great skin fast.</p>\n<p>Here she shares her insider tips.</p>\n<p><strong>Drink water (but not too much)</strong></p>\n<p>There’s no evidence that consuming a lot of water gives your skin a more hydrated appearance. Excess water is simply excreted by the kidneys. Too much water can actually be bad for you! Multiple sips throughout the day is far more effective.</p>\n<p><strong>Moisturise ASAP</strong></p>\n<p>Apply your serum or moisturiser within three minutes of cleansing. When the skin is wet, the cells are separated and are ripe and ready to suck in more moisture. Yes, it’s true.</p>\n<p><strong>Choose wisely</strong></p>\n<p>Serums and moisturisers rich in hyaluronic acid are the most effective way to achieve a fresh hydrated appearance. Apply plenty of moisturiser at bedtime so that it works overnight to give your skin that extra glow the following day.</p>\n<p><strong>Use serums</strong></p>\n<p>The beauty of serums is what they don’t contain. Basically it is just the fluid, rich in active ingredients without the occlusive agents, such as mineral oil and petroleum in creams/ointments, which can make the skin look excessively greasy or shiny during the daytime.</p>\n<p>I tend to recommend thicker moisturisers at night, layered over the top of a serum, and a serum alone beneath a moisturising sunscreen during the day. I also like the textural appearance of serums on the skin applied immediately before foundation.</p>\n<p><strong>Don’t overdo it</strong></p>\n<p>Protect the skin when exposed to severe environmental stress. The wind, dry air, chlorine, saunas/spas and extreme environmental temperatures will dry the skin out. Apply serums and moisturisers prior to exposing yourself to environmental extremes and then reapply immediately after exposure.</p>\n<p><strong>Cleanse gently</strong></p>\n<p>Avoid excessive use of harsh soap containing cleansers and excessive scrubbing. This will dry you out. Excessive cleansing removes natural protective moisturising factors that we need to retain skin protection and hydration.</p>\n<p><strong>Live well</strong></p>\n<p>Your diet counts. Eat a diet rich in fresh ingredients. Eat plenty of foods that are high in antioxidants, such as blueberries, and loads of greens, which are rich in vitamins that help nourish the skin. Avoid excessive alcohol and caffeine, which dehydrate you.</p>\n<p>Exercise regularly and practice yoga – too much stress shows on your skin.</p>\n<p><strong>Understand ingredients</strong></p>\n<p>Some ingredients are designed to exfoliate the skin and it’s good to know what they are. Topical retinoids are healthy for the skin, but can irritate if not used with a moisturising agent.</p>\n<p><strong>Be consistent</strong></p>\n<p>Use the same products daily. Sporadic adherence to a skin care routine leads to inferior results.</p>\n<p>&nbsp;</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/02/27130836/beauty1.jpg"},"categories":[{"slug":"skincare","name":"Skincare"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"cdbdc727-6ad5-5093-8881-c6add23932b2"}}

